@charset "UTF-8";
body {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Montserrat Alternates";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/MontserratAlternates-Regular.woff2") format("woff2"), url("../fonts/MontserratAlternates-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/MontserratAlternates-Bold.woff2") format("woff2"), url("../fonts/MontserratAlternates-Bold.woff") format("woff");
  font-display: swap;
}
.header {
  background-image: url("../img/mob/banner-mob.png");
  background-color: #ffffff;
  height: auto;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  box-sizing: border-box;
  position: relative;
}
@media (min-resolution: 2dppx) {
  .header {
    background-image: url("../img/mob/banner-mob@2x.png");
    background-size: 432px auto;
    min-height: 198px;
  }
}
@media screen and (min-width: 768px) and (min-resolution: 2dppx) {
  .header {
    background-image: url("../img/mob/banner-mob@2x.png");
    background-size: auto;
    min-height: 398px;
  }
}

.header__btn {
  width: 40px;
  height: 50px;
  padding: 10px 0;
  background-color: transparent;
  border: none;
  display: block;
  margin-left: auto;
  margin-right: 10%;
  color: #ffffff;
  font-family: inherit;
  font-size: 11px;
}

.header__title {
  color: #ffffff;
  font-weight: inherit;
  margin-top: 14%;
  margin-left: 8%;
  font-size: 28px;
  line-height: 30px;
}

.header::after {
  content: "";
  position: absolute;
  width: 395px;
  height: 20px;
  background-image: url("../img/mob/Line1.svg");
  background-repeat: no-repeat;
  bottom: -1%;
  left: 50%;
  transform: translate(-50%, -13%);
}

.intro {
  padding: 30px 0;
  display: flex;
  gap: 0 8%;
  align-items: center;
}

.intro__text {
  min-width: 152px;
  width: 40%;
}

.page {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
}

.when {
  position: relative;
  padding: 50px 0 20px;
  margin-bottom: 28px;
}

.when::after {
  content: "Когда?";
  position: absolute;
  color: #ffffff;
  padding: 6px 52px;
  font-size: 24px;
  font-weight: 400;
  width: 195px;
  height: 42px;
  background-image: url("../img/mob/title-brown.svg");
  background-repeat: no-repeat;
  top: -1%;
  left: 50%;
  transform: translate(-50%, -13%);
}

.when__text {
  text-align: center;
  position: relative;
}

.when__text:after {
  content: "";
  position: absolute;
  width: 228px;
  height: 92px;
  background-image: url("../img/mob/border-pink.svg");
  background-repeat: no-repeat;
  top: -1%;
  left: 50%;
  transform: translate(-50%, -13%);
}

.where {
  position: relative;
  padding: 50px 0 20px;
  display: flex;
}

.where::after {
  content: "Где?";
  position: absolute;
  color: #525453;
  padding: 6px 70px;
  font-size: 24px;
  font-weight: 400;
  width: 195px;
  height: 42px;
  background-image: url("../img/mob/title-pink.svg");
  background-repeat: no-repeat;
  top: -1%;
  left: 30%;
  transform: translate(-50%, -13%);
}

.where__text {
  text-align: center;
  position: relative;
}

.where__content {
  padding: 0 20px;
}

.where__content-name {
  display: block;
  margin-bottom: 10px;
}

.where__map {
  width: 200px;
  height: 202px;
}

.where__text:after {
  content: "";
  position: absolute;
  width: 228px;
  height: 92px;
  background-image: url("../img/mob/border-pink.svg");
  background-repeat: no-repeat;
  top: -1%;
  left: 50%;
  transform: translate(-50%, -13%);
}